import request from '@/utils/request'

// 获取订单数据
export const getOrderListApi = (dataType, page) => {
  return request.get('/order/list', {
    params: {
      dataType, // 订单类型
      page // 获取几页
    }
  })
}

// 获取订单详情
export const getOrderDetailApi = (orderId) => {
  return request.get('/order/list', {
    params: {
      orderId
    }
  })
}
