<template>
  <div class="order">
    <van-nav-bar title="我的订单" left-arrow @click-left="$router.go(-1)" />
    <van-tabs v-model="active" sticky>
      <van-tab name="all" title="全部"></van-tab>
      <van-tab name="payment" title="待支付"></van-tab>
      <van-tab name="delivery" title="待发货"></van-tab>
      <van-tab name="received" title="待收货"></van-tab>
      <van-tab name="comment" title="待评价"></van-tab>
    </van-tabs>

    <OrderListItem v-for="item in orderList" :key="item.order_id" :item="item"></OrderListItem>

    <!-- 底部换页 -->
    <div class="footer">
      <div class="pagectrl" v-if="orderList.length>0">
        <button @click="changePage(-1)" class="btn" v-if="nowPage>1">上一页</button>
        <div class="pageNum">当前页:{{nowPage}}</div>
        <button @click="changePage(1)" class="btn" v-if="nowPage<lastPage">下一页</button>
      </div>
      <div class="empty" v-else><span>{{StateObj[active]}}</span></div>
    </div>
  </div>
</template>

<script>
import OrderListItem from '@/components/OrderListItem.vue'
import { getOrderListApi } from '@/api/order'
export default {
  name: 'OrderPage',
  data () {
    return {
      active: this.$route.query.dataType || 'all',
      orderList: '', // 订单数据
      nowPage: 1, // 当前是第几页，每页订单共15条数据
      lastPage: -1, // 最后一页的页码，由服务器传回
      total: -1, // 多少笔订单，由服务器传回
      // 用与空订单的处理
      StateObj: {
        all: '订单为空，快去逛逛吧',
        payment: '待支付订单为空，快去逛逛吧',
        delivery: '待发货订单为空，快去逛逛吧',
        received: '待收货订单为空，快去逛逛吧',
        comment: '待评价订单为空，快去逛逛吧'
      }
    }
  },
  created () {
    this.getOrderList()
  },
  methods: {
    async getOrderList () {
      const { data: { list } } = await getOrderListApi(this.active, this.nowPage)
      // 服务器传来的订单列表，每个订单中只有单件商品的数量，而没有该订单中所有的商品数量，所以要加
      // goods_total_num表示每个订单中的商品总数
      list.data.forEach(item => {
        item.goods_total_num = item.goods.reduce((sum, elem) => {
          return sum + elem.total_num
        }, 0)
        return item.goods_total_num
      })
      this.orderList = list.data
      this.lastPage = list.last_page
      // console.log(list)
    },
    changePage (num) {
      if (num > 0) {
        // 关于当前页面是否小于1或大于lastPage这个判断，已经在button的v-if中判断了，所以这里不用再写判断了
        this.nowPage++
      } else {
        this.nowPage--
      }
    }
  },
  watch: {
    active: {
      handler (value) {
        this.getOrderList()
      }
    },
    nowPage: {
      handler (value) {
        this.getOrderList()
      }
    }
  },
  components: {
    OrderListItem
  }
}
</script>

<style lang="less" scoped>
.order {
  background-color: #fafafa;
}
.van-tabs {
  position: sticky;
  top: 0;
}
.footer{
  .pagectrl{
    display: flex;
    justify-content: center;
    margin-bottom: 15px;
    align-items: center;
    .btn{
      padding: 0;
      margin: 0;
      border: none;
      width: 50px;
      height: 30px;
      background-color:#EEE5DE;
      border-radius:5px ;
      &:active{
        background-color:#e3e5e7
      }
    }
    .pageNum{
      margin-left: 5px;
      margin-right: 5px;
    }
  }
  .empty{
    width: 100%;
    height: 700px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url('@/assets/empty.png') no-repeat center 0
    ;
  }
}
</style>
